:root {
  --list-link-color: #00acdc;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__link {
  width: 40px;
  height: 40px;
}

.logo__image {
  width: 100%;
}

.list__items {
  margin: 0;
  padding: 0;
  display: flex;
}

.list__item {
  list-style-type: none;
}

.list__item:not(:first-child) {
  margin-left: 20px;
}

.list__item-link {
  text-transform: uppercase;
  color: #000;
  padding: 23px 0;
  text-decoration: none;
  display: block;
}

.list__item-link:hover {
  color: var(--list-link-color);
}

.is-active {
  color: var(--list-link-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--list-link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

